import React, { useState } from "react";
import { Col } from "react-bootstrap";

import "./pages-css/layout.css";
import { Link, navigate } from "gatsby"
import Primo3D from "../images/primo-3d.gif"
import AyudaIcon from "../images/iconos/btn_ayuda.svg"
import { ThreeCircles } from "react-loader-spinner"
import { Modal } from "react-bootstrap"

export default function Menu ({ backUrl, poliza, setSelected, selected }) {

  const windowGlobal = typeof window !== 'undefined' && window;
  const [loading, setLoading] = useState(false);

  function quitar(){
    setLoading(false);
  }

  function dataUrlStart(){
    var myItem = windowGlobal.localStorage.getItem('token');
    windowGlobal.localStorage.clear();
    windowGlobal.localStorage.setItem('token', myItem);
    navigate("/")
  }

  function dataUrl(){
    if(backUrl === "/"){
      var myItem = windowGlobal.localStorage.getItem('token');
      windowGlobal.localStorage.clear();
      windowGlobal.localStorage.setItem('token', myItem);
      navigate(backUrl)
    } else {
      navigate(backUrl)
    }
  }

  var timer;

  function clickModal(event) {
    clearTimeout(timer);
    
    if (event.detail === 1) {
      timer = setTimeout(() => {
        //console.log("SINGLE CLICK");
      }, 200)

    } else if (event.detail === 2) {
      console.log("DOUBLE CLICK, abre modal para opciones de id sucursal");

    }
  }

  const componenteModal = () => {
    return(
      <Modal  centered id="modal-content">
        <Modal.Header style={{display: 'flex', justifyContent: 'start'}}>
          <Modal.Title>Cambio de id de sucursal:</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <table>
            <td>Guasaa</td>
            <td>sdfsdf</td>
          </table>
        </Modal.Body>
        <Modal.Footer>

        </Modal.Footer>
      </Modal>
    )
  }
 //, background: 'rgba(255,182,157,0.2)',  border: '1px solid salmon'
  return backUrl == null ? (
    <>
      <div className={loading == false ? `loading` : `loading active`}>
        <ThreeCircles
          height="50%"
          width="50%"
          color="white"
          ariaLabel="three-circles-rotating"
          className="iconLoading"
        />
      </div>
      <div onClick={(e) => clickModal(e)} style={{height: '35%', width: '25%', position: 'absolute', cursor: 'pointer', zIndex: 100}}>

      </div>
      <div className="margenMenu">
        <center className="areaPosition">
          <img src={Primo3D} alt="primo rivero 3d" width={"70%"} />
        </center>
      </div>
    </>
  ) : (
    <>
      <div className={loading == false ? `loading` : `loading active`} onClick={() => quitar()}>
        <h1 className={"text-warning"}>
          En un momento un asesor vendr para ayudarte.
        </h1>
        <ThreeCircles
          height="50%"
          width="50%"
          color="white"
          ariaLabel="three-circles-rotating"
          className="iconLoading"
        />
      </div>
      <div className="margenMenu" >
        <center className="areaPosition">
          {
              poliza && poliza?.clase && poliza?.clase.length > 0 ? (
                  <label >
                    <input class="item-check" name="keywords[]" type="radio" value="1" />
                    <Col style={{border: '0.5px solid #858796', margin: 5, padding: 5}} className="item-name" 
                        onClick={(e) => {
                          //console.log("Pulsado jiji")
                          setSelected({
                              ...selected,
                              Clase: poliza.clase,
                              Precio: poliza.total,
                              Id: poliza.id
                          })}}
                        >
                        <Col style={{background: 'white', padding: 10, borderRadius: 8}}>
                            <div className="p-1" style={{backgroundColor: '#dd3446', color: 'white'}}>
                                <center><h5 style={{marginTop: '0.5rem'}}>{poliza?.clase} GM</h5></center>
                            </div>
                            <div className="texto-azul" style={{marginBottom: 25}}>
                                <center><h3 style={{marginTop: '0.5rem'}}></h3></center>
                            </div>
                            <div className="texto-azul" style={{marginBottom: 25}}>
                                <center><h3 style={{marginTop: '0.5rem'}}>$ {poliza?.total}</h3></center>
                            </div>
                            <div>
                                {poliza?.incluye.map((subObj, subIndex) => (
                                    <li key={subIndex} className="list-group-item fila-incluye" style={{fontSize: 15, marginBottom: 0, margin: 7, justifyContent: 'flex-start', display: 'flex'}}><img style={{width: 20, height: 20, marginRight: 14, marginLeft: 14}} src={subObj.imagen}/>{subObj.nombre}</li>
                                ))}
                            </div>
                        </Col>
                    </Col>
                  </label>
              ): null
          }
          {/*<div className="btnAyuda" onClick={() => setLoading(true)}>
            <img src={AyudaIcon} alt="ayuda" width={"30%"} />
            <p>Necesito ayuda</p>
          </div>*/}
          <div onClick={() => dataUrlStart()} >
            <div className="btn btn-naranja shadow py-4 mx-4 my-3 text-white">
              Salir
            </div>
          </div>
          <div onClick={() => dataUrl()}>
            <div className="btn btn-back shadow py-4 mx-4 my-3 text-white">
              Regresar
            </div>
          </div>
        </center>
      </div>
    </>
  )
}
